*{
  border: 0;
  margin: 0;
  box-sizing: border-box;
}
.body{
  min-height: 100vh;
  background: black;
}
.body .container{
  position: relative;
  width: 100%;
  overflow:hidden;
}
.bubble{
  position: relative;
  display: flex;
}
.bubble span{
  position: relative;
  width: 20px;
  height: 20px;
  background-color: aqua;
  margin:0 4px;
  border-radius: 50%;
  box-shadow: 0 0 0 10 rgba(0, 255, 255, 0.027),
  0 0 0 50px aqua,
  0 0 0 100px aqua;
  animation: animated 15s linear infinite;
  animation-duration: calc(100s/var(--i));
}
.bubble span:nth-child(even){
  background-color: brown;
  box-shadow: 0 0 0 10px brown,
  0 0 0 50px brown,
  }
@keyframes animated {
  0%{
      transform: translateY(100vh) scale(0);
  }
  100%{
      transform: translateY(-10vh) scale(1);
  }
  
}